<template>
  <v-card>
    <v-data-table
      :headers="headers"
      :items="roles"
      :options.sync="options"
      :server-items-length="totalRoles"
      disable-sort
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>角色列表</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <Create-Role @getRoles="getRoles"></Create-Role>
          <v-spacer></v-spacer>
        </v-toolbar>
      </template>

      <template v-slot:item.enabled="{item}">
        <Value-Mapper :map="{true: '是', false: '否'}" :mapKey="item.enabled"></Value-Mapper>
      </template>

      <template v-slot:item.actions="{ item }">
        <Update-Role :roleProp="item" @getRoles="getRoles"></Update-Role>
        <Delete-Role :roleId="item.id" @getRoles="getRoles"></Delete-Role>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    options: {},
    totalRoles: 0,
    headers: [
      {
        text: "角色名",
        align: "start",
        sortable: false,
        value: "name"
      },
      { text: "启用", value: "enabled" },
      { text: "描述", value: "description" },
      { text: "操作", value: "actions", sortable: false, align: "end" }
    ],
    roles: []
  }),

  watch: {
    options: {
      handler() {
        this.getRoles();
      },
      deep: true
    }
  },

  created() {
    this.getRoles();
  },

  methods: {
    getRoles() {
      this.$http
        .post("/user/api/v1/role/page", {
          pageNum: this.options.page,
          pageSize: this.options.itemsPerPage
        })
        .then(res => {
          this.totalRoles = res.data.total;
          this.roles = res.data.records;
        })
        .catch(error => {});
    }
  },

  components: {
    CreateRole: () => import("./component/roles/CreateRole"),
    UpdateRole: () => import("./component/roles/UpdateRole"),
    DeleteRole: () => import("./component/roles/DeleteRole"),
    ValueMapper: () => import("@/views/common/component/ValueMapper")
  }
};
</script>